import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Isvg from 'react-inlinesvg';
import { useParams } from 'react-router-dom';
import { PopularAricles } from '../components/PopularArticles';
import { Subscribe } from '../components/Subscrube';
import { Title } from '../components/Title';
import { HOST_NAME } from "../config";
import { DefaultLayout } from '../containers/DefaultLayout';
import InstagramBlogPost from '../images/InstagramBlogPost.png';
import Fb from '../images/svg/fb.svg';

const getFormattedDateJSX = (timestamp) => {
    const publishedDate = new Date(timestamp);
    const monthName = publishedDate.toLocaleString('default', { month: 'long' });
    const day = publishedDate.getDate();
    const year = publishedDate.getFullYear();
    return <span>{monthName.toUpperCase()} {day}, {year}</span>;
}

export const GuideBlogPost = () => {
    const { slug } = useParams();
    const [guide, setGuide] = useState(null);

    // Load the guide data
    useEffect(() => {
        const url = HOST_NAME + "api/guide/";
        const params = new URLSearchParams();
        params.append("slug", slug);
        axios.get(url, {params: params}).then(response => {
            console.log(response.data);
            if(response.status === 200 && response.data.status === 'ok') {
                setGuide(response.data.data);
            }
        });
    }, [slug]);

    return (
        <div className="property-blog-post-wrapper">
            <DefaultLayout>
                {guide &&
                    <div className="property-blog-post">
                        <div className="property-blog-post-container">
                            <div className="heading">
                                <img className="post-image" src={guide.thumbnail[0]} alt="Blog post" />
                                <h1>{guide.details.title[0]}</h1>
                                <div className="post-info">
                                    <h6>from <span>{guide.details.creator}</span></h6>
                                    {
                                        getFormattedDateJSX(guide.timestamp)
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="property-blog-post-container">
                            <div className="take-photo">
                                <span>Photo : {guide.details.creator}</span>
                                <p>Located in the very heart of the Bay of Palma, Puerto Portals is the most prestigious nautical and leisure complex of the mediterranean.</p>
                            </div>

                            <div className="content-blog-post">
                                <div className="social-buttons">
                                    <Isvg src={Fb} />
                                    <Isvg src={Fb} />
                                    <Isvg src={Fb} />
                                    <Isvg src={Fb} />
                                </div>
                                <div className="content">
                                    <p>Puerto Portals  is one of the most prestigious yachting marinas in the Mediterranean. Tucked
                                        away on the southwestern coast of Mallorca, the port attracts the international jet-set crowd,
                                        who enjoy the first-class restaurants and boutiques in a relaxed waterfront setting.
                                    </p>
                                    <p> One of the most desirable holiday and residential locations
                                        in Mallorca, with green hills in the background, white-sand
                                        beaches, and an endless turquoise sea, the port exudes
                                        glitz and
                                        glamo—the perfect base for sailing luxury yachts. Ten kilometres from the capital city of Palma,
                                        and 16 km from the airport, Puerto Portals is well connected to all  the  key  areas  in the island’s southwest, a region renowned for its natural beauty and the Tramuntana Mountain range, a
                                        UNESCO World Heritage Site.</p>
                                    <div className="instagram-post">
                                        <Title text="Top 3 activities" />
                                        <img className="instagram-post-image" src={InstagramBlogPost} alt="Blog post instagram" />
                                        <h2>SAILING</h2>
                                        <p>With 650 mooring spaces offering the sleekest of superyachts, one of the port’s main attractions is, quite
                                            clearly, sailing. Puerto Portals is a vibrant nautical and leisure complex, hosting a range of events
                                            throughout the year, including the Dragon Winter Race and the Sailing Week, part of the TP52 Super Series
                                            international circuit. If you do not own your own yacht, you can charter one or head out on one of the
                                            many excursions to Palma or the island’s southeast. The Sailing School offers private sailing classes to
                                            adults and children, alongside windsurfing and other watersporting activities giving you the chance to
                                            explore the sea that surrounds the island, to see it from a different perspective. </p>
                                    </div>
                                    <div className="instagram-post">
                                        <Title text="Top 3 activities" />
                                        <img className="instagram-post-image" src={InstagramBlogPost} alt="Blog post instagram" />
                                        <h2>SAILING</h2>
                                        <p>With 650 mooring spaces offering the sleekest of superyachts, one of the port’s main attractions is, quite
                                            clearly, sailing. Puerto Portals is a vibrant nautical and leisure complex, hosting a range of events
                                            throughout the year, including the Dragon Winter Race and the Sailing Week, part of the TP52 Super Series
                                            international circuit. If you do not own your own yacht, you can charter one or head out on one of the
                                            many excursions to Palma or the island’s southeast. The Sailing School offers private sailing classes to
                                            adults and children, alongside windsurfing and other watersporting activities giving you the chance to
                                            explore the sea that surrounds the island, to see it from a different perspective. </p>
                                    </div>
                                    <div className="instagram-post">
                                        <Title text="Top 3 activities" />
                                        <img className="instagram-post-image" src={InstagramBlogPost} alt="Blog post instagram" />
                                        <h2>SAILING</h2>
                                        <p>With 650 mooring spaces offering the sleekest of superyachts, one of the port’s main attractions is, quite
                                            clearly, sailing. Puerto Portals is a vibrant nautical and leisure complex, hosting a range of events
                                            throughout the year, including the Dragon Winter Race and the Sailing Week, part of the TP52 Super Series
                                            international circuit. If you do not own your own yacht, you can charter one or head out on one of the
                                            many excursions to Palma or the island’s southeast. The Sailing School offers private sailing classes to
                                            adults and children, alongside windsurfing and other watersporting activities giving you the chance to
                                            explore the sea that surrounds the island, to see it from a different perspective. </p>
                                    </div>
                                    <div className="instagram-post">
                                        <Title text="Top 3 activities" />
                                        <img className="instagram-post-image" src={InstagramBlogPost} alt="Blog post instagram" />
                                        <h2>SAILING</h2>
                                        <p>With 650 mooring spaces offering the sleekest of superyachts, one of the port’s main attractions is, quite
                                            clearly, sailing. Puerto Portals is a vibrant nautical and leisure complex, hosting a range of events
                                            throughout the year, including the Dragon Winter Race and the Sailing Week, part of the TP52 Super Series
                                            international circuit. If you do not own your own yacht, you can charter one or head out on one of the
                                            many excursions to Palma or the island’s southeast. The Sailing School offers private sailing classes to
                                            adults and children, alongside windsurfing and other watersporting activities giving you the chance to
                                            explore the sea that surrounds the island, to see it from a different perspective. </p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <PopularAricles />
                        <Subscribe blog />

                    </div>
                }
            </DefaultLayout>
        </div>
    )
}

export default GuideBlogPost